.home {
	display: flex;
	width: 100%;
	height: 100%;
	.home-image {
		width: 50%;
		display: flex;
		justify-content: center;
		align-content: center;
		img {
			width: 100%;
			user-drag: none;
			user-select: none;
			-moz-user-select: none;
			-webkit-user-drag: none;
			-webkit-user-select: none;
			-ms-user-select: none; } }
	.home-content {
		flex: 1;
		height: 100%;
		display: flex;
		flex-direction: column;
		justify-content: center;
		padding-left: 100px;
		h1 {
			margin: 0;
			margin-bottom: 15px;
			line-height: 1.2;
			color: #262B32; }
		p {
			line-height: 1.6; }
		button {
			cursor: pointer;
			background-color: #262B32;
			border: 0;
			color: #ccc;
			padding: 10px 30px;
			font-size: 1rem;
			border-radius: 10px;
			display: flex;
			width: fit-content;
			text-transform: uppercase;
			transition: all .2s ease-in-out;
			margin-top: 20px;
			outline: none;
			&:hover {
				opacity: .8; } } } }

// style right
.home.right {
	flex-direction: row-reverse;
	.home-content {
		padding-right: 100px;
		padding-left: 0; } }

// style no-image
.home.no-image {
	.home-content {
		padding-right: 0;
		padding-left: 0; }
	.home-image {
		display: none; } }
